/* Reset CSS
* --------------------------------------- */
	body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,
	form,fieldset,input,textarea,p,blockquote,th,td {
		padding: 0;
		margin: 0;
	}
	a{
		text-decoration:none;
	}
	table {
		border-spacing: 0;
	}
	fieldset,img {
		border: 0;
	}
	address,caption,cite,code,dfn,em,strong,th,var {
		font-weight: normal;
		font-style: normal;
	}
	strong{
		font-weight: bold;
	}
	ol,ul {
		list-style: none;
		margin:0;
		padding:0;
	}
	caption,th {
		text-align: left;

	}
	h1,h2,h3,h4,h5,h6 {
		font-weight: normal;
		font-size: 100%;
		margin:0;
		padding:0;
		color:#444;
	}
	q:before,q:after {
		content:'';
	}
	abbr,acronym { border: 0;
	}
	.hidden{
		display:none;
	}
	.dont-show{
		width:0;
		height:0;
		overflow:hidden;
		position:relative;
		left:-9999px;
	}


/// 반응형 매니저
/// @access public
/// @param {String} $breakpoint - 브레이크포인트
	$breakpoints: (
	'too-small': (max-width: 385px),
	'mid-small': (max-width: 475px),
	'small': (max-width: 550px),
	);
/// @requires $breakpoints
	@mixin respond-to($breakpoint) {
	$raw-query: map-get($breakpoints, $breakpoint);

	@if $raw-query {
		$query: if(
		type-of($raw-query) == 'string',
		unquote($raw-query),
		inspect($raw-query)
		);

		@media #{$query} {
		@content;
		}
	} @else {
		@error 'No value found for `#{$breakpoint}`. '
			+ 'Please make sure it is defined in `$breakpoints` map.';
	}
	}
	
	/*웹폰트(kopub체) */
	@font-face {
		font-family: 'kopub';
		src:url('../font/KoPubDotumMedium.eot') format('embedded-opentype'),
			url("../font/KoPubDotumMedium.woff") format('woff');
	}

	@font-face {
		font-family: 'kopublight';
		src:url('../font/KoPubDotumLight.eot') format('embedded-opentype'),
			url("../font/KoPubDotumLight.woff") format('woff');
	}

	@font-face {
		font-family: 'kopubbold';
		src:url('../font/KoPubDotumBold.eot') format('embedded-opentype'),
			url("../font/KoPubDotumBold.woff") format('woff');
	}



//CommonStyle
	*{
		font-family:'kopub';
	}
	body{
		font-family:'kopub';
		width:100%;
		height:100%;
		background:url('/img/background/01_back.jpg');
		background-size:cover;
		transition:all 0.5s;
		&.join{
			background:url('/img/background/02_back.jpg');
			background-size:cover;
			overflow:hidden;
			transition:all 0.5s;
		}
		&.social{
			background:url('/img/background/03_back.jpg');
			background-size:cover;
			overflow:hidden;
			transition:all 0.5s;
		}
		&.fixed{
			position:fixed;
		}
		h1,h2,h3,h4,strong,b{
			font-weight:normal;
			//font-family:'kopubbold';
		}
		p{
			font-size:15px;
			@include respond-to('too-small'){
				font-size:13px;
			}
		}
	}
	img.icon{
		height:20px;
		width:auto;
		position:relative;
		top:5px;
		@include respond-to('too-small'){
			height:18px;
			width:auto;
		}
	}
//Scrollbar
	::-webkit-scrollbar
	{
	width: 5px;  /* for vertical scrollbars */
	height: 5px; /* for horizontal scrollbars */
	}

	::-webkit-scrollbar-track
	{
	background: rgba(255, 255, 255, 0.1);
	}

	::-webkit-scrollbar-thumb
	{
	background: rgba(255, 255, 255, 0.5);
	}

